// Here you can create and customize the pages' header (Navigation):

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%header {
  position: absolute;
  width: 100%;
  border-bottom: 1px solid #0b0a0a38;
  background-color: $section-bg-clr;
  box-shadow: 0 0 10px rgb(113, 113, 113);
  .container {
    display: flex;
    align-items: center;
    justify-content: space-between;
    position: relative;
    min-height: 75px;
    img {
      width: 80px;
    }
    nav {
      @include breakpoint(tablet) {
        flex: 1;
      }
      ul {
        display: none;
        position: absolute;
        top: 101.5%;
        left: 0;
        width: 100%;
        background-color: $section-bg-clr;
        @include breakpoint(tablet) {
          display: flex !important;
          position: relative;
          align-items: center;
          justify-content: flex-end;
        }
        li {
          @include breakpoint(tablet) {
            margin: 0 15px;
          }
          a {
            display: block;
            position: relative;
            text-align: center;
            width: 100%;
            color: $black;
            padding: 5px 0;
            transition: $transition;
            border-radius: 5px;
            border-bottom: 2px solid $black;
            &:hover {
              font-weight: bold;
              color: $main-clr;
              background-color: $black-alt;
            }
            @include breakpoint(tablet) {
              font-size: rem(18);
              border: none;
              border-radius: unset;
              padding: 5px 20px;
              &::before {
                position: absolute;
                content: "";
                height: 2px;
                width: 0;
                left: 50%;
                transform: translateX(-50%);
                bottom: 0;
                transition: $transition;
                background-color: $main-clr;
              }
              &:hover {
                font-weight: normal;
                background-color: unset;
                &::before {
                  width: calc(100% - 40px);
                }
              }
            }
          }
        }
      }
      .toggle_menu {
        @extend %toggle_menu;
      }
    }
  }
}
