// This is a small lib For Scroll up buttons:
@use "../helpers" as *;

// 1- >> radius scroll up:

%scroll-up {
  display: none;
  position: fixed;
  color: #fff;
  height: 40px;
  width: 40px;
  right: 20px;
  bottom: 20px;
  border: none;
  cursor: pointer;
  z-index: 5;
  border-radius: 50%;
  transition: $transition;
  background-color: #10cab7;
  &:hover {
    background-color: #2c4755;
  }
}
