// Here You can style and customize the home page:

@use "../globals" as *;
@use "../mixins" as *;
@use "../helpers" as *;
@use "../components" as *;

// <> How to organize the CSS properties:
// 1 - >> display stuff:
// 2 - >> positioning stuff:
// 3 - >> box-model stuff:
// 4 - >> typography:
// 5 - >> manipulation:
// 6 - >> misc:

// ------- start header Section -------//
header {
  @extend %header;
}
/* -------- End header Section --------*/
/* ------- Start Landing Section ------*/
.landing {
  background-color: lightblue;
  background-image: url("../imgs/landing.jpg");
  background-size: cover;
  background-repeat: no-repeat;
  height: 100vh;
  .container {
    display: flex;
    text-align: center;
    align-items: center;
    justify-content: center;
    height: 100%;

    .intro-text {
      h1 {
        font-size: rem(50);
        color: $main-clr;
      }
      p {
        font-size: rem(19);
        line-height: $line-height;
      }
    }
  }
}
/* -------- End Landing Section -------*/
/* ------- Start Features section -----*/
.features {
  padding-top: $section-padding;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 50px;
    text-align: center;
    i {
      color: $main-clr;
    }
    h3 {
      margin: 15px auto;
    }
    p {
      line-height: $line-height;
      color: $txt-clr;
      font-size: rem(18);
      max-width: 500px;
      margin: 0 auto;
    }
  }
}
/* -------- End Features section ------*/
/* ------- Start Services section -----*/
.services {
  padding-top: $txt-padding-md;
  padding-bottom: $section-padding;
  @extend %section_header-3;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    column-gap: 50px;
    .srv {
      margin-bottom: 50px;
      text-align: center;
      @include breakpoint(laptop) {
        display: flex;
        column-gap: 15px;
      }
      i {
        color: $main-clr;
      }
      .text {
        h3 {
          margin: 15px 0;
        }
        p {
          max-width: 500px;
          margin: 0 auto;
          color: $gray-txt-clr;
          line-height: $line-height;
        }
        @include breakpoint(laptop) {
          text-align: start;
          h3 {
            margin-top: unset;
          }
        }
      }
    }
    .img-holder {
      position: relative;
      display: none;
      width: 250px;
      @include breakpoint(desktop) {
        display: block;
        &::after {
          position: absolute;
          content: "";
          height: calc(100% + 80px);
          width: 100px;
          top: -40px;
          right: -40px;
          z-index: -1;
          border-radius: $border-radius;
          background-color: $secondary-clr;
        }
      }
      img {
        border-radius: $border-radius;
      }
    }
  }
}
/* -------- End Services section ------*/
.scroll-up {
  @extend %scroll-up;
}
/* ------- Start Portfolio section ----*/
.portfolio {
  padding-top: $txt-padding-md;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  @extend %section_header-3;
  .container {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(300px, 1fr));
    gap: 50px;
  }
  article {
    overflow: hidden;
    transition: $transition;
    border-radius: $border-radius;
    box-shadow: $box-shadow;
    background-color: #fff;
    &:hover {
      box-shadow: $box-shadow-2;
      transform: translateY(-10px);
    }
    .text {
      padding: 15px;
      h3 {
        margin-bottom: 10px;
      }
      p {
        color: $gray-txt-clr;
      }
    }
  }
}
/* -------- End Portfolio section -----*/
/* --------- Start About Section ------*/
.about {
  padding-top: $txt-padding-md;
  padding-bottom: $section-padding;
  @extend %section_header-3;
  .container {
    @include breakpoint(laptop) {
      display: flex;
      column-gap: 50px;
      align-items: center;
    }
  }
  .img-holder {
    position: relative;
    margin: 0 auto 50px;
    width: 250px;
    height: 375px;
    @include breakpoint(laptop) {
      &::before {
        position: absolute;
        content: "";
        height: calc(100% + 80px);
        width: 100px;
        top: -40px;
        left: -20px;
        z-index: -1;
        border-radius: $border-radius;
        background-color: #ebeced;
      }
      &::after {
        position: absolute;
        content: "";
        height: calc(100% - 80px);
        width: 101px;
        top: -40px;
        right: -125px;
        border-radius: $border-radius;
        border-left: 80px solid $main-clr;
        border-bottom: 80px solid $main-clr;
        z-index: -1;
      }
    }
    img {
      border-radius: 5px;
    }
  }
  .text {
    flex-basis: 50%;
    text-align: center;
    @include breakpoint(laptop) {
      text-align: start;
      hr {
        margin: 30px 0 !important;
      }
    }
    p:first-of-type {
      font-weight: bold;
    }
    p:last-of-type {
      color: $gray-txt-clr;
    }
    hr {
      width: 50%;
      margin: 30px auto;
      color: $main-clr;
    }
  }
}

/* ---------- End About Section -------*/
/* -------- Start Contact Section -----*/
.contact {
  text-align: center;
  padding-top: $txt-padding-md;
  padding-bottom: $section-padding;
  background-color: $section-bg-clr;
  @extend %section_header-3;
  h3 {
    font-size: 25px;
    letter-spacing: -1px;
    color: $secondary-clr;
  }
  h4 {
    font-size: 20px;
    margin: 15px auto;
    a {
      color: $main-clr;
    }
  }
  .social-icons {
    margin-top: 15px;
    a:not(:last-of-type) {
      margin-right: 20px;
    }
    a:nth-child(1) {
      padding: 10px;
      color: blue;
    }
    a:nth-child(2) {
      padding: 10px;
      color: #0f6aed;
    }
    a:nth-child(3) {
      padding: 10px;
      color: red;
    }
    a {
      transition: $transition;
      border-radius: $border-radius;
      &:hover {
        background-color: #30cdb76e;
      }
    }
  }
}

/* --------- End Contact Section ------*/
/* ------- Start Footer section -------*/
footer {
  @extend %footer;
}
/* -------- End Footer section --------*/
