// Here you can create and customize the pages' footer:

@use "../globals" as *;
@use "../helpers" as *;
@use "../mixins" as *;

%footer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 50px;
  text-transform: capitalize;
  color: #fff;
  background-color: $secondary-clr;
  span:last-of-type {
    font-weight: bold;
    color: $main-clr;
  }
}
