@use "../globals" as *;
@use "../helpers" as *;

%toggle_menu {
  display: flex;
  align-items: flex-end;
  flex-direction: column;
  cursor: pointer;
  @include breakpoint(tablet) {
    display: none;
  }
  span {
    display: block;
    height: 3px;
    width: 25px;
    margin-bottom: 5px;
    transition: $transition;
    background-color: $black;
    &:nth-of-type(2) {
      width: 15px;
      transition: $transition;
    }
  }
  &:hover span:nth-of-type(2) {
    width: 25px;
  }
}
